import { AssembleeHistoryComponent } from './assemblee-history/assemblee-history.component';
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes } from '@angular/router';
import { AssembleeComponent } from './assemblee/assemblee.component';
import { AssembleePastorComponent } from './assemblee-pastor/assemblee-pastor.component';
import { AssembleeStaffComponent } from './assemblee-staff/assemblee-staff.component';
import { OurBeliefComponent } from './our-belief/our-belief.component';
import { StepperModule } from 'ng-uikit-pro-standard';
import { UIKIT_BASE } from '@shared/ui/ui-kit.asset';

const routes: Routes = [
  {
    path: '',
    component: AssembleeComponent,
    data: { title: 'Shekinkah Tabernacle: Assemblee' }
  },
  {
    path: 'notre-croyance',
    component: OurBeliefComponent,
    data: { title: 'Shekinkah Tabernacle: Notre Croyance' }
  },
  {
    path: 'pastor',
    component: AssembleePastorComponent,
    data: { title: 'Shekinkah Tabernacle: Pastor' }
  },
  {
    path: 'assemblee-staff',
    component: AssembleeStaffComponent,
    data: { title: 'Shekinkah Tabernacle: Staff' }
  },
  {
    path: 'morijah',
    component: AssembleeHistoryComponent,
    data: { title: 'Shekinkah Tabernacle: Morijah' }
  }
];
@NgModule({
  declarations: [
    AssembleeComponent,
    AssembleePastorComponent,
    AssembleeStaffComponent,
    AssembleeHistoryComponent,
    OurBeliefComponent
  ],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    ...UIKIT_BASE,
    StepperModule
  ],
  providers: []
})
export class LayoutChurchModule {}
